<template>
  <div id="app">
    <div class="main">
      <div class="more-button" v-if="!seen">
        <router-link to="/"><button class="mb" v-on:click="seen = true">На главную</button></router-link>
      </div>
      <router-view></router-view>
      <MainTm id="default" v-if="seen"/>
      <div class="more-button" v-if="seen">
        <router-link :to="{ name: 'gallery' }"><button class="mb" v-on:click="seen = !seen">посмотреть порфолио</button></router-link>
      </div>
      <Form v-if="seen" />
      <Footer />
    </div>
  </div>
</template>

<script>
import MainTm from './components/MainTm'
import Form from './components/Form'
import Footer from './components/Footer'

export default {
  name: 'App',
  components: {
    MainTm,
    Form,
    Footer
  },
  data () {
    return {
      toResume: true,
      seen: true
    }
  }
}
</script>



